				"html": "<h4>Suggested Reading:</h4>\n<!-- Please add any articles you think might be helpful to read before writing the article -->\n<ul>\n<li><a href=\"https://en.wikipedia.org/wiki/Supervised_learning\">https://en.wikipedia.org/wiki/Supervised_learning</a></li>\n<li><a href=\"https://stackoverflow.com/a/1854449/6873133\">https://stackoverflow.com/a/1854449/6873133</a></li>\n</ul>\n<h4>Draft of Article:</h4>\n<!-- Please add your working draft below in GitHub-flavored Markdown -->\n<!--\nDiscussion points:\n- Supervised learning is task when you have a response you're trying to predict\n- Ideas of training data to use\n- Use test data to test learning from training data\n- Think about complexity of learning model\n- Should validate your learning with methods such as cross-validation\n- Should be wary of overfitting\n- Give examples e.g. logistic regression, support vector machines, neural networks\nTakeaway points:\n- What is special about supervised learning?\n- In what scenario would you use it in?\n- Caveats or traps to think about?\n- What are some example models? \n-->\n<p>What is Supervised Learning ?</p>\n<p>Supervised learning is a type of machine learning algorithm that uses a known data-set (called the training data-set) to make predictions.</p>\n<p>Category of supervised learning:</p>\n<p>Regression: In a regression problem, we are trying to predict results within a continuous output, meaning that we are trying to map input variables to some continuous function.\nClassification: In a classification problem, we are instead trying to predict results in a discrete output. In other words, we are trying to map input variables into discrete categories.</p>\n<p>Example 1:</p>\n<p>Given data about the size of houses on the real estate market, try to predict their price. Price as a function of size is a continuous output, so this is a regression problem.</p>\n<p>Example 2:</p>\n<p>(a) Regression - For continuous-response values. For example given a picture of a person, we have to predict their age on the basis of the given picture</p>\n<p>(b) Classification - for categorical response values, where the data can be separated into specific “classes”. For example given a patient with a tumor, we have to predict whether the tumor is malignant or benign.</p>",
